
.CheckOut .title {
    padding : 30px 30px 30px 0;
    border-bottom: 1px solid #eaeaea;
    margin-bottom:40px;
}

.CheckOut .title div {
    width: 80%;
    margin: auto;
}
.CheckOut .title p {
    color:#3FB871;
    font-size:1.2em;
    font-weight: bold;
}
.CheckOut div {
    background:white;
    margin : 5  px 0px;
}
.CheckOut .info {
    border-bottom: 1px solid #eaeaea;
    margin-bottom:30px;
    padding-bottom:30px;
}
.CheckOut .info div {
    width: 80%;
    margin: auto;  
}
.CheckOut .info div div {
    margin-top:20px;
}
.CheckOut .info input {
    padding:5px;
    width:300px;
}
.CheckOut .info button {
    background:none;
    border:1px solid #3FB871 ;
    cursor: pointer;
    font-size: 0.9em;
    padding: 6px 11px;
    margin-left:20px;
}
.CheckOut .info p {
    color:#3FB871;
    font-size:1.05em;
    font-weight:500;
    margin-bottom: 15px;
}
.CheckOut .ship {
    border-bottom: 1px solid #eaeaea;
    margin-bottom:30px;
    padding:20px 0px 50px 0;
}
.CheckOut .ship div {
    width: 80%;
    margin: auto;
}
.CheckOut .ship div div {
    margin-top:30px;
}
.CheckOut .ship span {
    color:#0055AA;
}
.CheckOut .ship button  {
    background : none;
    border : 1px solid #3FB871;
    margin : 0 5px;
    padding:5px;
    cursor: pointer;
}
.CheckOut .total {
    width: 80%;
    margin: auto;
    text-align: right;
}
.CheckOut .total .wrapper {
    margin-left: auto;
    width:300px;
    border:1px solid #eaeaea;
    padding: 20px;
    display:grid;
    grid-template-columns: repeat(2,120px);
    grid-template-rows: repeat(3,30px);
    margin-bottom: 30px;
}
.CheckOut .left1 {
    grid-column: 1/2;
    grid-row:1/2;
}
.CheckOut .left2 {
    grid-column: 1/2;
    grid-row:2/3;
}
.CheckOut .left3 {
    grid-column: 1/2;
    grid-row:3/4;
}
.CheckOut .right1 {
    grid-column: 2/3;
    grid-row:1/2;
}
.CheckOut .right2 {
    grid-column: 2/3;
    grid-row:2/3;
}
.CheckOut .right3 {
    grid-column: 2/3;
    grid-row:3/4;
}
.CheckOut .bottom {
    grid-column: 1/3;
    grid-row:4/5;
}
 .bottom button {
    width:200px;
    background: #3FB871;
    color:white;
    border:none;
    padding : 10px 0;
    font-size: 1.1em;
    cursor: pointer;
}
.CheckOut .cod, .CheckOut .airpay, .CheckOut .card {
    margin-top:30px;
}
.CheckOut .card input {
    margin-left:20px;
}



