.filter_item .hidden{
    display: none;
}
.label{
    position: relative;
    display: block;
    /* float: left; */
    margin-right: 10px;
    width: 14px;
    height: 14px;
    border: 1px solid #C8CCD4;
    border-radius: 100%;
    -webkit-tap-highlight-color: transparent;
}
.label::after{
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background: #225CFF;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    opacity: .08;
    pointer-events: none;
}
.filter_item:hover .label::after{
    -webkit-transform: scale(3.6);
            transform: scale(3.6);
}
input[type="radio" ]:checked + .label {
    border-color: #225cff;
}
input[type="radio"]:checked + .label:after {
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 0.2s cubic-bezier(0.35, 0.9, 0.4, 0.9);
    transition: all 0.2s cubic-bezier(0.35, 0.9, 0.4, 0.9);
    opacity: 1;
}



