      text-decoration: none;
      color: white;
  }
  .NavBar li:last-child, .NavBar .search__icon, .NavBar .search--sm, .NavBar--sm {
    display:none;
  }
  /* Effect hover */
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm) {
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    background: #3FB871;
    -webkit-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
  }
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0; 
    bottom: 0;
    left: 0;
    right: 0;
    background: rgb(46, 145, 115);
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 50%;
    transform-origin: 50%;
    -webkit-transition-property: transform;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
  }
  
  
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):hover,
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):focus,
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):active {
    color: white;
  }
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):hover:before,
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):focus:before,
  .NavBar li:not(.toggleIcon):not(.search__icon):not(.search--sm):not(.cart--sm):active:before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
