.st0{font-family:'FootlightMTLight';}
.st1{font-size:83.0285px;}
.st2{fill:gray;}

svg{
  width: 500px;
    height: 400px;
    text-align: center;
    fill: #3FB871;
}
@media only screen and (max-width: 768px) {

  svg{
    width: 320px;
     
  }
}
path#XMLID_5_ {
   
    fill: #3FB871;
    filter: url(#blurFilter4);
}
path#XMLID_11_ ,path#XMLID_2_ {
    fill: #3FB871;
}
.circle{
  animation: out 2s infinite ease-out;
  fill: #3FB871;
}

#container_Notfound{
  text-align:center;
  margin-top: 150px;
}
.btnBack{
    display: inline-block;
    font-size: 17px;
    color: #fff;
    padding: 10px 30px;
    font-weight: bold;
    background-color: #3FB871;
    border: none;
    outline: none;
    border-radius: 2px;
}
.message{
    color:#3FB871;
    margin: 30px 0;
    font-size: 20px;
}
.message:after{
	content:"]";
}
.message:before{
	content:"[";
}

.message:after, .message:before {

  color: #16a085;
  font-size: 20px;
  -webkit-animation-name: opacity;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-name: opacity;
          animation-name: opacity;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
          margin:0 50px;
}

@-webkit-keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes opacity {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

@keyframes out {
  0% {r:1;  opacity: 0.9 ;}
  25%{r:5;  opacity: 0.3 ;}
  50%{r:10; opacity: 0.2 ;}
  75%{r:15;opacity:0.1;}
 	100% {r:20;opacity:0;}
}